.login-container {
    display: flex;
    height: 100vh; /* Ocupa toda la altura de la ventana */
  }
  
  .slider {
    flex: 1.5; /* Ocupa la mitad izquierda de la pantalla */
    background-color: #ffffff; /* Color de fondo del slider */
    display: flex;
    justify-content: center;
    align-items: center;
    /* Aquí puedes agregar más estilos para el slider */
  }

  
  .login-form {
    flex: 1; /* Ocupa la mitad derecha de la pantalla */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px; /* Espaciado interno */
    background-color: #B40B0B; /* Color de fondo del formulario */
  }
  
  input {
    padding: 10px;
    width: 80%; /* Ancho de los inputs */
    border: 1px solid #C40E0E; /* Borde de los inputs */
    border-radius: 5px; /* Bordes redondeados */
    margin-bottom: 5px;
    background-color: #C40E0E; /* Color de fondo del input (puedes cambiarlo) */
    transition: background-color 0.3s, color 0.3s; /* Transiciones suaves para el color */
}

input::placeholder {
    color: #FFFFFF; /* Color del placeholder */
    opacity: 1; /* Asegúrate de que el placeholder sea completamente visible */
}

input:hover {
    background-color: #C40E0E; /* Cambia el color de fondo al pasar el mouse */
    color: #FFFFFF; /* Asegura que el color del texto se mantenga blanco */
}
  
  button {
    padding: 10px 200px; /* Espaciado interno del botón */
    background-color: #ffffff; /* Color de fondo del botón */
    color: #B40B0B; /* Color del texto del botón */
    border: none; /* Sin borde */
    border-radius: 5px; /* Bordes redondeados */
    cursor: pointer; /* Cambia el cursor al pasar sobre el botón */
    margin-top: 30px; /* Aumenta el margen superior para separar del texto */
    margin-bottom: 10px
  }
  
  button:hover {
    background-color: #ffffff; /* Color al pasar el mouse sobre el botón */
  }
  
  a {
    margin-top: 5px; /* Espaciado superior del enlace */
    color: #ffffff; /* Color del enlace */
    text-decoration: none; /* Sin subrayado */
  }
  
  a:hover {
    text-decoration: underline; /* Subrayar al pasar el mouse */
  }

  .container {
        display: flex;
        justify-content: space-between;
        align-items: center; /* Alinea verticalmente los elementos */
        gap: 160px; /* Espacio entre los elementos hijos */
        margin-bottom: 1px; /* Espacio entre la fila y el contenido inferior */
        color: #ffffff;
    }
    .texto-con-opacidad {
        opacity: 0.7; /* Ejemplo de opacidad */
        
    }

  h4 {
    margin-right: 420px;
    color: #ffffff;
    margin-top: 10px;
    margin-bottom: 5px /* Aumenta el margen superior para separar del texto */
  }

  h2 {
    color: #ffffff;
    margin-bottom: 25px
  }

  h3 {
    font-size: 18px;
    margin-bottom: 5px
  }

  h5 {
    color: #ffffff;
    font-size: 14px;
    margin-bottom: 1px
  }

  .button-container {
    display: flex; /* Usar flexbox para alinear los botones en la misma línea */
    justify-content: center; /* Centrar los botones en el contenedor */
    gap: 10px; /* Espacio entre los botones */
}

.btn-google, .btn-facebook {
    padding: 9px 85px; /* Aumenta el padding para hacer los botones más grandes */
    font-size: 16px; /* Aumenta el tamaño de la fuente */
    border: 1px solid #FFFFFF; /* Borde blanco de 2 píxeles */
    border-radius: 5px; /* Bordes redondeados */
    color: white; /* Color del texto */
    background-color: transparent; /* Fondo transparente para que se vea el borde */
    cursor: pointer; /* Cambia el cursor al pasar por el botón */
    transition: background-color 0.3s, color 0.3s; /* Transición suave para el color de fondo y texto */
}

.btn-google {
    background-color: #B40B0B; /* Color de fondo para el botón de Google */
}

.btn-facebook {
    background-color: #B40B0B; /* Color de fondo para el botón de Facebook */
}

.btn-google:hover, .btn-facebook:hover {
    background-color: rgba(255, 255, 255, 0.2); /* Cambia el fondo al pasar el mouse */
    color: #FFFFFF; /* Asegura que el color del texto sea blanco al pasar el mouse */
}

