/* src/styles/Register.css */
.register-container {
    background-color: white;
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    margin: 5rem auto;
  }
  
  .register-container h2 {
    margin-bottom: 1rem;
  }
  
  .register-container input {
    width: 100%;
    padding: 0.5rem;
    margin-bottom: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .register-container button {
    width: 100%;
    padding: 0.5rem;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    color: white;
    font-size: 1rem;
    cursor: pointer;
  }
  
  .register-container button:hover {
    background-color: #0056b3;
  }
  
  .register-container .error {
    color: red;
    margin-bottom: 1rem;
  }
  